  --shadow-color: #1412237c;
  --light-color: #ffffff7c;
  --text-color: #4b5bab;
}

body {
  color: var(--text-color);
  text-align: center;
}

.btn {
  background-color: transparent;
  border: none;
  color: var(--text-color);
  font-family: "Annie Use Your Telescope";
  font-size: 20px;
  font-weight: 600;
  cursor: pointer;
  transition: 0.1s;
}

.btn:hover {
  opacity: 0.7;
  transition: 0.1s;
}

.header {}

.title {
  color: var(--text-color);
  font-size: 48px;
  font-family: "Amatic SC";
}

.main {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-end;
  gap: 16px;
}

@media (max-width: 768px) {

  .main {
    flex-direction: column;
    align-items: center
  }
}

.css-editor {}

.css-editor__input {}

.html-hover {
  font-weight: 600;
}

.planet-hover,
.planet-hover moon {
  box-shadow: inset 0 0 15px rgba(255, 255, 255, 0.55),
    0px 0px 15px rgba(12, 66, 167, 0.8);
}`, "",{"version":3,"sources":["webpack://./global.css"],"names":[],"mappings":"AAEA;EACE,yBAAyB;EACzB,wBAAwB;EACxB,qBAAqB;AACvB;;AAEA;EACE,wBAAwB;EACxB,kBAAkB;AACpB;;AAEA;EACE,6BAA6B;EAC7B,YAAY;EACZ,wBAAwB;EACxB,uCAAuC;EACvC,eAAe;EACf,gBAAgB;EAChB,eAAe;EACf,gBAAgB;AAClB;;AAEA;EACE,YAAY;EACZ,gBAAgB;AAClB;;AAEA,SAAS;;AAET;EACE,wBAAwB;EACxB,eAAe;EACf,wBAAwB;AAC1B;;AAEA;EACE,aAAa;EACb,eAAe;EACf,uBAAuB;EACvB,qBAAqB;EACrB,SAAS;AACX;;AAEA;;EAEE;IACE,sBAAsB;IACtB;EACF;AACF;;AAEA,aAAa;;AAEb,oBAAoB;;AAEpB;EACE,gBAAgB;AAClB;;AAEA;;EAEE;uCACqC;AACvC","sourcesContent":["@import url('https://fonts.googleapis.com/css2?family=Amatic+SC:wght@400;700&family=Annie+Use+Your+Telescope&family=Cutive+Mono&display=swap');\n\n:root {\n  --shadow-color: #1412237c;\n  --light-color: #ffffff7c;\n  --text-color: #4b5bab;\n}\n\nbody {\n  color: var(--text-color);\n  text-align: center;\n}\n\n.btn {\n  background-color: transparent;\n  border: none;\n  color: var(--text-color);\n  font-family: \"Annie Use Your Telescope\";\n  font-size: 20px;\n  font-weight: 600;\n  cursor: pointer;\n  transition: 0.1s;\n}\n\n.btn:hover {\n  opacity: 0.7;\n  transition: 0.1s;\n}\n\n.header {}\n\n.title {\n  color: var(--text-color);\n  font-size: 48px;\n  font-family: \"Amatic SC\";\n}\n\n.main {\n  display: flex;\n  flex-wrap: wrap;\n  justify-content: center;\n  align-items: flex-end;\n  gap: 16px;\n}\n\n@media (max-width: 768px) {\n\n  .main {\n    flex-direction: column;\n    align-items: center\n  }\n}\n\n.css-editor {}\n\n.css-editor__input {}\n\n.html-hover {\n  font-weight: 600;\n}\n\n.planet-hover,\n.planet-hover moon {\n  box-shadow: inset 0 0 15px rgba(255, 255, 255, 0.55),\n    0px 0px 15px rgba(12, 66, 167, 0.8);\n}"],"sourceRoot":""}]);
