export class Button {
    createResetButton(rootClass) {
        const button = document.createElement('button');
        button.textContent = 'Reset';
        button.classList.add(`${rootClass}__btn`, `${rootClass}__btn_reset`, 'btn_reset', 'btn');
        return button;
    }
    createHelpButton(rootClass) {
        const button = document.createElement('button');
        button.textContent = 'Help';
        button.classList.add(`${rootClass}__btn`, `${rootClass}__btn_help`, 'btn');
        return button;
    }
    createEnterButton(rootClass) {
        const button = document.createElement('button');
        button.textContent = 'Enter';
        button.classList.add(`${rootClass}__btn`, `${rootClass}__btn_enter`, 'btn');
        return button;
    }
    createOkayButton(rootClass) {
        const button = document.createElement('button');
        button.textContent = 'Okay';
        button.classList.add(`${rootClass}__btn`, `${rootClass}__btn_okay`, 'btn_okay', 'btn');
        return button;
    }
}
